@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

* {
  font-family: "Open Sans", sans-serif;
}

body, html {
  background-color: #262525;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.fullscreen-success {
  position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(141, 231, 132);
    display: flex;
    justify-content: center;
    flex-direction: column;
    z-index: 999;
    opacity: 0;
  transition: all 0.5s;
}

.fullscreen-success .fullscreen-success__content {
  padding: 20px;
}

.fullscreen-success h1 {
  font-size: 1.3rem;
  color: #262525;
  margin: 0;
}

.fullscreen-success .userPhoto {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin: 20px 0;
}