/* Контейнер для всего приложения */
.face-recognition-container {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background: #000;
}

/* Видео с камерой занимает весь экран */
.camera-feed {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;
}

/* Оверлей в верхней части экрана для логотипа и часов */
.overlay-top {
    position: fixed;
    top: 20px;
    left: 20px;
    right: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 3;
}

/* Логотип с эффектом наведения */
.logo {
    width: 80px;
    cursor: pointer;
    transition: transform 0.3s ease;
}

.logo:hover {
    transform: scale(1.1);
}

/* Часы с полупрозрачным фоном */
.clock {
    background: rgba(0, 0, 0, 0.6);
    color: #fff;
    padding: 10px 20px;
    border-radius: 10px;
    font-size: 24px;
    font-family: Arial, sans-serif;
}

/* Панель управления (контролы) */
.controls-panel {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    background: rgba(255, 255, 255, 0.9);
    padding: 15px 25px;
    border-radius: 10px;
    z-index: 4;
    display: flex;
    gap: 20px;
    align-items: center;
    font-family: Arial, sans-serif;
    color: #333;
    flex-direction: column;
}

.control-item {
    display: flex;
    flex-direction: column;
}

/* Оверлей для успешного распознавания */
.fullscreen-success {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
    pointer-events: none;
}

.fullscreen-success__content {
    text-align: center;
    color: #fff;
}

.fullscreen-error {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(199, 34, 34, 0.8);
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
    pointer-events: none;
}

.fullscreen-error__content {
    text-align: center;
    color: #fff;
}

/* Стилизация изображения пользователя в оверлее */
.userPhoto {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin-bottom: 20px;
    border: 4px solid #fff;
    object-fit: cover;
}

.fullscreen-success__content h1 {
    color: #fff;
}